import React from 'react';
import '../App.css';
import ReactAvatar from 'react-avatar';
import { 
  BrowserRouter as Router, 
} from 'react-router-dom'; 
import Box from '@material-ui/core/Box';

var classes = "Null";
var fees = "Null";
var experience = "Null";
var timings = "10:00AMâ€“8:00PM";
var bigImage = "https://lh3.googleusercontent.com/TZHmt_vOEEBTJ4ZQNkQbOgQWwOpF2ZQe1zlgI9oE67jRwDf5LOuVGz_04RiVWxNdnGRtW8aP=w1080-h608-p-no-v0";
var profileImage = "https://cdn2.iconfinder.com/data/icons/royal-crowns/512/royal-alphabet-crown-letter-english-k-512.png";
var colorOverlay = "rgba(19,137,253, 0.5)";

export default function K5MusicAcademy(){
    return (
        <Router>
        <div class="App">
          <div class="wallpaper">
            <img src={bigImage} alt="Avatar" class="image"/>
            <div class="overlay" style={{ background: colorOverlay}}>
              <div class="avatar">
              <ReactAvatar src={profileImage} round="50%" size="150"/>
              </div>
            </div>
          </div>
          <div class="details">
          <Box boxShadow={2} bgcolor="transparent">
              <table class="table">
                <tr class="tr">
                  <td class="th">{classes}</td>
                  <td class="th">{fees}</td>
                  <td class="th">{experience}</td>
                </tr>
                <tr>
                  <td align="center">Classes</td>
                  <td align="center">Fees</td>
                  <td align="center">Experience</td>
                </tr>
              </table>
          </Box>
          </div>
    
          <div class="container">
              <div class="head">General</div>
              <div class="info">
                <div class="field">Age</div>
                <div class="attribute">Any</div>
              </div>
              <div class="info">
                <div class="field">Type</div>
                <div class="attribute">Dance, Music</div>
              </div>
              <div class="info">
                <div class="field">Duration</div>
                <div class="attribute">1hr/day</div>
              </div>
              <div class="info">
                <div class="field">Timings</div>
                <div class="attribute">{timings}</div>
              </div>
              <div class="head">Wall of Fame</div>
              
              <div class="head">Qualifications</div>
              <div class="head">Eligibility</div>
              <div class="head">Vision and Mission</div>
            </div>
    
        </div>
        </Router>
      );
}


